// Spacing

$spacer: 1rem !default;
$spacers: () !default;
$spacers: map-merge(
  (
    6: ($spacer * 4),
    7: ($spacer * 6),
    8: ($spacer * 8),
    9: ($spacer * 10),
    10: ($spacer * 12),
    11: ($spacer * 14),
    12: ($spacer * 16),
    13: ($spacer * 18),
    14: ($spacer * 20)
  ),
  $spacers
);

// Sizes

$sizes: () !default;
$sizes: map-merge(
 (
  10: 10%,
  20: 20%,
  30: 30%,
  33: 33%,
  40: 40%,
  60: 60%,
  70: 70%,
  80: 80%,
  85: 85%,
  90: 90%,
 ),
 $sizes
);

// Color system

$white: #fff !default;
$gray-100: #f4f3f8 !default;
$gray-200: #e9ecef !default;
$gray-300: #dee2e6 !default;
$gray-400: #ced4da !default;
$gray-500: #adb5bd !default;
$gray-600: #6c757d !default;
$gray-700: #495057 !default;
$gray-800: #343a40 !default;
$gray-900: #212529 !default;
$black: #000 !default;

$grays: () !default;
// stylelint-disable-next-line scss/dollar-variable-default
$grays: map-merge(
 (
  "100": $gray-100,
  "200": $gray-200,
  "300": $gray-300,
  "400": $gray-400,
  "500": $gray-500,
  "600": $gray-600,
  "700": $gray-700,
  "800": $gray-800,
  "900": $gray-900,
 ),
 $grays
);

$purple: #9a10a1 !default;
$primary: #9a10a1 !default;
$secondary: #5e02f8 !default;
$success: #34bd91 !default;
$info: #18b7ff !default;
$warning: #fdc724 !default;
$danger: #bd4242 !default;
$light: #e8f1fc !default;
$dark: #10080e !default;
$sky: #e9f4ff !default; // new
$pistachio: #ddf5f4 !default; // new
$darkblue: #1f2c73 !default; // new

$theme-colors: () !default;
$theme-colors: map-merge(
 (
  "purple": $purple,
  "sky": $sky,
  "pistachio": $pistachio,
  "darkblue": $darkblue,
 ),
 $theme-colors
);

// Responsive breakpoints

$grid-breakpoints: (
 xs: 0,
 sm: 576px,
 md: 768px,
 lg: 992px,
 xl: 1240px,
 xxl: 1500px,
 xxxl: 1700px,
) !default;

$container-max-widths: (
 sm: 540px,
 md: 100%,
 lg: 100%,
 xl: 1230px,
) !default;

// Config

$enable-responsive-font-sizes: true !default;
$border-radius: 0.25rem !default;
$border-radius-lg: 0.35rem !default;
$border-radius-sm: 0.25rem !default;

// Typography
$font-family-base: "NeutraWeb", sans-serif !default;
$headings-font-family: rift, sans-serif !default;
$headings-font-weight: 700 !default;
$headings-line-height: 1 !default;
$line-height-base: 1.55 !default;
$headings-margin-bottom: $spacer / 1 !default;
$display1-size: 118px !default;
$display2-size: 5rem !default;
$display3-size: 4rem !default;
$display4-size: 3.4rem !default;
$font-size-sm: 1rem !default;
$font-size-xs: 0.9rem !default; // new
$text-muted: #6b2459 !default;
$font-weight-bold: 600 !default;
$font-weight-bolder: 700 !default;
$font-size-base: 1.25rem !default;
$lead-font-size: 2.2rem !default;
$lead-font-weight: 400 !default;
$h2-font-size: 52px !default;
$font-size-lg: $font-size-base * 1.2 !default;

// Shadows

$box-shadow-sm: 0 0.125rem 0.25rem rgba(35, 38, 45, 0.09) !default;
$box-shadow: 0 0.5rem 1.25rem rgba(35, 38, 45, 0.125) !default;
$box-shadow-lg: 0 1rem 3rem rgba(35, 38, 45, 0.275) !default;
$box-shadow-light-sm: 0 0.125rem 0.25rem rgba(35, 38, 45, 0.05) !default; // new
$box-shadow-light: 0 0.5rem 1.25rem rgba(35, 38, 45, 0.05) !default; // new
$box-shadow-light-lg: 0 1rem 3rem rgba(35, 38, 45, 0.05) !default; // new

// Navbar

$navbar-padding-y: 0.8rem !default;
$navbar-nav-link-padding-x: 1.2rem !default;
$navbar-light-color: #212529 !default;
$navbar-light-hover-color: #3d448b !default;
$navbar-light-active-color: #3d448b !default;
$navbar-light-toggler-border-color: transparent !default;
$navbar-dark-color: #fff !default;
$navbar-dark-hover-color: #e2e5e8 !default;
$navbar-dark-toggler-border-color: transparent !default;
$navbar-dropdown-box-shadow-lg: 0 1.5rem 3rem rgba(75, 78, 88, 0.175) !default; // new

// Footer

$footer-padding-y: 4.25rem !default; // new
$footer-header-font-size: 1.25rem !default; // new
$footer-link-font-size: 0.875rem !default; // new
$footer-text-font-size: 0.7rem !default; // new

$footer-light-header-color: $dark !default; // new
$footer-light-link-color: $darkblue !default; // new
$footer-light-social-icon-bg: $dark !default; // new
$footer-light-social-icon-color: $light !default; // new
$footer-light-text-color: rgba($dark, 0.8) !default; // new
$footer-light-brand-color: $dark !default; // new

$footer-dark-header-color: $info !default; // new
$footer-dark-link-color: $sky !default; // new
$footer-dark-social-icon-bg: $sky !default; // new
$footer-dark-social-icon-color: $dark !default; // new
$footer-dark-text-color: rgba($sky, 0.6) !default; // new
$footer-dark-brand-color: $sky !default; // new

// Alerts

$alert-bg-level: -9 !default;
$alert-color-level: 8 !default;

// Dropdowns

$dropdown-font-size: 0.95rem !default;
$dropdown-border-color: #fbfbfb !default;
$dropdown-link-color: #495057 !default;
$dropdown-link-hover-color: #454e9e !default;
$dropdown-link-active-color: #454e9e !default;
$dropdown-link-hover-bg: none !default;
$dropdown-link-active-bg: none !default;
$dropdown-padding-y: 1rem !default;

// Buttons
$input-btn-font-size: 1.075rem !default;
$btn-padding-y: 0.4rem !default;
$btn-padding-x: 1.1rem !default;
$btn-padding-y-sm: 0.3rem !default;
$btn-padding-x-sm: 0.6rem !default;
$btn-padding-y-lg: 0.9rem !default;
$btn-padding-x-lg: 3rem !default;
$btn-font-size-lg: 20px !default;

// Badges

$badge-padding-y: 0.25rem !default;
$badge-padding-x: 0.75rem !default;
$badge-font-weight: 400 !default;
$badge-border-radius: 0.35rem !default;
$badge-pill-padding-x: 0.75rem !default;
$badge-padding-y-lg: 0.7rem !default; // new
$badge-padding-x-lg: 1.1rem !default; // new
$badge-font-size-lg: 0.9rem !default; // new

// Cards

$card-spacer-x: 1.3rem !default;
$card-border-width: 0 !default;

// Forms

$input-placeholder-color: rgba(#000, 0.35) !default;
$input-focus-box-shadow: none !default;
$input-focus-border-color: $primary !default;
$input-group-addon-bg: #fff !default;

// Custom forms

$custom-control-indicator-bg: #d1d5dc !default;
$custom-control-indicator-border-width: 0 !default;
$custom-switch-width: 2.5rem !default;
$custom-switch-indicator-size: 1rem !default;
$custom-control-indicator-focus-box-shadow: none !default;
$custom-select-border-color: #e9ecef !default;

// Pagination

$pagination-color: $dark !default;
$pagination-bg: transparent !default;
$pagination-border-width: 0 !default;
$pagination-hover-color: $dark !default;
$pagination-hover-bg: #f8f9fa !default;
$pagination-active-bg: $dark !default;
$pagination-active-color: $light !default;
$pagination-padding-x: 0.85rem !default;

// Breadcrumbs

$breadcrumb-bg: none !default;
$breadcrumb-padding-x: 0 !default;
$breadcrumb-active-color: $darkblue !default;

// Modals

$modal-backdrop-opacity: 0.85 !default;
$modal-backdrop-bg: $dark !default;

// Tables

$table-cell-padding: 1rem !default;
$table-border-color: #e9ecef !default;
$table-accent-bg: $light !default;
$table-hover-bg: darken($light, 2%) !default;
$table-dark-bg: lighten($dark, 6%) !default;
$table-dark-border-color: lighten($dark, 10%) !default;

// Transitions

$bg-transition: background-color 0.2s linear !default; // new

// Navs

$nav-pills-link-active-color: $dark !default;
$nav-pills-link-active-bg: #fff !default;

// Iconav

$iconav-brand-padding-x: 1rem !default; // new
$iconav-brand-padding-y: 1.75rem !default; // new

$iconav-light-bg: #fff !default; // new
$iconav-light-border-color: #eee !default; // new
$iconav-light-brand-bg: #f3f4f7 !default; // new
$iconav-light-link-color: $darkblue !default; // new
$iconav-light-hover-bg: #f8f9fa !default; // new
$iconav-light-active-bg: #f4f4f7 !default; // new

$iconav-dark-bg: #30343e !default; // new
$iconav-dark-border-color: #212225 !default; // new
$iconav-dark-brand-bg: #0f1013 !default; // new
$iconav-dark-link-color: #e9ecef !default; // new
$iconav-dark-hover-bg: darken(#30343e, 5%) !default; // new
$iconav-dark-active-bg: #20232b !default; // new

// Timeline

$timeline-bg: #e4e9f1 !default; // new
$timeline-post-margin-top: 5rem !default; // new

// Statcards

$statcard-padding-y: 1.5rem !default; // new
$statcard-padding-x: 1.5rem !default; // new
$statcard-change-size: 0.8rem !default; // new
$statcard-label-size: 0.8rem !default; // new
$statcard-label-font-weight: 500 !default; // new
$statcard-label-color: $text-muted !default; // new

// Z-index

$zindex-navbar: 1034 !default; // new
$zindex-navbar-ecommerce: 1035 !default; // new
$zindex-iconav: 1036 !default; // new

// Fixed sizes

$fixed-sizes: 150, 200, 250, 300, 350, 400, 450, 500, 550, 600, 650, 700 !default; // new
